import React, { Component } from 'react';
import { Accordion, AccordionTab } from 'primereact/accordion';
import '../App.css';
export class AccordionDemo extends Component {

    constructor(props) {
        super(props);

        this.state = {
            activeIndex: null
        }
    }

    onClick(itemIndex) {
        let activeIndex = this.state.activeIndex ? [...this.state.activeIndex] : [];

        if (activeIndex.length === 0) {
            activeIndex.push(itemIndex);
        }
        else {
            const index = activeIndex.indexOf(itemIndex);
            if (index === -1) {
                activeIndex.push(itemIndex);
            }
            else {
                activeIndex.splice(index, 1);
            }
        }

        this.setState({ activeIndex });
    }

    render() {
        return (
            <div className="accordion-demo">
                <div className="card" style={{ paddingLeft: '20px', paddingRight: '20px' }}>
                    {/* activeIndex={0} */}
                    <Accordion  >
                        <AccordionTab header="Qu necesito para tener mi propia escuela?" >
                            <p>No se requiere experiencia previa en la enseanza, pero s es necesario tener una fuerte vocacin empresarial para gestionar tu propio negocio.
                                No es imprescindible que tengas un alto nivel de ingls, ni que domines varios idiomas, ya que diferenciaos entre la direccin de la franquicia y la parte pedaggica.</p>
                        </AccordionTab>
                        <AccordionTab header="Necesito un local para ser franquiciado?">
                            <p>Slo si quieres abrir tu propia escuela, en EpicGroup Lab tenemos dos modelos de negocio:
                                Con el modelo EPIC LICENSE no necesitas local, ni realizar una gran inversin, ya que trabajars desarrollando nuestras actividades para nios en colegios y centros educativos.
                                Con el modelo EPIC CENTER necesitars un local a partir de 60 m2 en el que abrir tu escuela.</p>
                        </AccordionTab>
                        <AccordionTab header="Qu poblacin mnima se requiere para abrir una escuela?">
                            <p>Con los dos modelos recomendamos localidades a partir de 20.000 habitantes para garantizar la rentabilidad de tu inversin.</p>
                        </AccordionTab>
                        <AccordionTab header="Qu formacin recibir por parte de Epic?">
                            <p>Como franquiciado es fundamental que domines todas las reas del negocio, por lo que recibirs una amplia formacin desde el principio.
                                Te formaremos a nivel comercial, metodolgico, tcnico y laboral para que puedas gestionar y dirigir tu escuela. Adems, tus mentores recibirn formacin pedaggica continuada para trabajar con nuestra metodologa.</p>
                        </AccordionTab>
                    </Accordion>
                </div>
            </div>
        )
    }
}